<style lang="stylus" rel="stylesheet/stylus">
    @import "../../common/stylus/mixin.styl"
    .star
      height: 20px
      width: 100%
      position: relative
      ul
       position: absolute
       width: 200px
       left: 50%
       margin-left: -100px
      .star-48
        height: 20px
        float: left
        & > li
          list-style none
          float: left
          width: 20px
          height: 20px
          background-size:20px 20px
          margin-right: 22px
          &:last-child
            margin-right: 0 px;
          &.on
            bg-image('star48_on')
          &.half
            bg-image('star48_half')
          &.off
            bg-image('star48_off')
      .star-36
        height: 15px
        float: left
        & > li
          list-style none
          float: left
          width: 15px
          height: 15px
          background-size:15px 15px
          margin-right: 6px
          &:last-child
            margin-right: 0 px;
          &.on
            bg-image('star36_on')
          &.half
            bg-image('star36_half')
          &.off
            bg-image('star36_off')
      .star-24
        height: 10px
        float: left
        & > li
          list-style none
          float: left
          width: 10px
          height: 10px
          background-size:10px 10px
          margin-right: 3px
          &:last-child
            margin-right: 0 px;
          &.on
            bg-image('star24_on')
          &.half
            bg-image('star24_half')
          &.off
            bg-image('star24_off')
</style>

<template>

 <div class="star">
    <ul :class="sizeClass">
      <li v-for="item in starNum" :class="item"></li>
    </ul>
 </div>
</template>

<script>
  export default {
    props: {
      size: {
        type: Number
      },
      score: {
        type: Number
      }
    },
    computed: {
      sizeClass () {
        return 'star-' + this.size;
      },
      itemClass () {
        let arr = [];
        for (let i = 0; i < this.score; i++) {
          arr.push('1');
        }
        console.log(arr);
        return arr;
      },
      starNum () {
        let arr2 = [];
        for (let i = 1; i < this.score; i++) {
          arr2.push('on');
        }
        let x = this.score - parseInt(this.score);
        if (x < 0.5 && x > 0) {
          arr2.push('half');
        } else {
          arr2.push('on');
        }
        while (arr2.length < 5) {
          arr2.push('off');
        }
        console.log(arr2);
        return arr2;
      }
    }

  };
</script>



