<style lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/mixin.styl"
  *{
    list-style none
    margin: 0
    padding: 0
  }
  .goods
    display: flex
    position: absolute
    top: 174px
    bottom: 46px
    width: 100%
    overflow: hidden
    .menu-wraper
      width: 80px
      background-color: #f3f5f7
      .menu-item
        display: table
        width: 56px
        height: 54px
        padding: 0 12px
        font-size: 12px
        line-height: 14px
      .current
         background-color:#ddd;
        .menu-title
          display: table-cell
          vertical-align: middle
          border-1px(rgba(7, 17, 27, 0.1))
          .icon
            display: inline-block
            vertical-align: top
            width: 12px
            height: 12px
            background-size: 12px 12px
            &.decrease
              bg-image('decrease_3')
            &.discount
              bg-image('discount_3')
            &.guarantee
              bg-image('guarantee_3')
            &.invoice
              bg-image('invoice_3')
            &.special
              bg-image('special_3')
    .foods-wrapper
      flex: 1
      .title
        padding-left: 14px
        height: 26px
        line-height: 26px
        border-left:2px solid #d9dde1
        font-size:12px
        color:rgb(147,153,159)
        background: #f3f5f7
      .food-item
        display:flex
        margin: 18px
        padding-bottom :18px
        border-1px(rgba(7,17,27,0.1))
        &:last-child
          border-none()
          margin-bottom: 0
        .icon
          flex:0 0 57px
          margin-right: 10px
        .content
          flex: 1
          .name
            margin:2px 0 8px 0
            height: 14px
            line-height: 14px
            font-size :14px
            color :rgb(7,17,27)
          .desc, .extra
            line-height: 10px
            font-size:10px
            color:rgb(147,153,159)
          .desc
            line-height: 12px
            margin-bottom: 8px
          .extra
            .count
              margin-right: 12px
          .price
            font-weight: 700
            line-height: 24px
            .now
              margin-right: 8px
              font-size:14px
              color :rgb(240,20,20)
            .old
              text-decoration line-through
              font-size :10px
              color:rgb(147,153,159)
          .cartcontroller-wrapper
            position: absolute
            right: 0
            bottom: 12px
</style>

<template>
  <div class="goods">
    <div class="menu-wraper" ref="menuWraper">
      <ul>
        <li v-for="(item,index) in goods"
            @click="_selectMenu(index)"
            class="menu-item"
            :class="{current:currentIndex===index}"
            ref="menuList">
          <div class="menu-title border-1px">
            <span v-show="item.type > 0" class="icon" :class="classMap[item.type]"></span>
            {{item.name}}
          </div>
        </li>
      </ul>
    </div>
    <div class="foods-wrapper" ref="foodsWrapper">
      <ul>
        <li v-for="item in goods" class="food-list" ref="foodList">
          <h1 class="title">{{item.name
            }}</h1>
          <ul>
            <li @click.stop="selectFood(food,$event)" v-for="food in item.foods" class="food-item border-1px">
              <div class="icon">
                <img width="57" height="57" :src="food.icon">
              </div>
              <div class="content">
                <h2 class="name">{{food.name

                  }}</h2>
                <p class="desc">{{food.description}}</p>
                <div class="extra">
                  <span class="count">月售{{food.sellCount}}份</span><span>好评率{{food.rating}}%</span>
                </div>
                <div class="price">
                  <span class="now">￥{{food.price}}</span><span class="old"
                                                                v-show="food.oldPrice">￥{{food.oldPrice}}</span>
                </div>
                <div class="cartcontroller-wrapper">
                  <cartController :food="food" @add="addFood"></cartController>
                </div>
              </div>
            </li>
          </ul>
        </li>
      </ul>
    </div>
    <shop-cart :deliveryPrice="seller.deliveryPrice"
               :selectFoods="selectFoods"
               ref="shopcart">
    </shop-cart>
    <food @add="addFood" :food="selectedFood" ref="food"></food>
  </div>
</template>

<script>
  import BScroll from 'better-scroll';
  import cartController from '../cartcontroller/cartcontrol.vue';
  import shopCart from '../shopcart/shopcart.vue';
  import food from '../food/food.vue';

  export default {
    data () {
      return {
        goods: [],
        listHeight: [],
//        currentIndex: 2
        scrollY: 0,
        selectedFood: {}
      };
    },
    props: {
      seller: {
        type: Object
      }
    },
    components: {
      cartController,
      shopCart,
      food
    },
    computed: {
      currentIndex () {
        for (let i = 0; i < this.listHeight.length; i++) {
          if (!(this.listHeight[i + 1]) || (this.scrollY >= this.listHeight[i] && this.scrollY < this.listHeight[i + 1])) {
            this._followScroll(i + 1);
            return i;
          }
        }
      },
      selectFoods () {
        let foods = [];
        this.goods.forEach((good) => {
          good.foods.forEach((food) => {
            if (food.count) {
              foods.push(food);
            }
          });
        });
        console.log(foods);
        return foods;
      }
    },
    // 滑动事件:
    methods: {
      selectFood (food, event) {
        console.log(1);
        if (!event._constructed) {
          return;
        }
        this.selectedFood = food;
        this.$refs.food.show();
      },
      addFood (el) {
        this.$refs.shopcart.drop(el);
      },
      _initScroll () {
        /* eslint-disable no-new */
        new BScroll(this.$refs.menuWraper);
//        new BScroll(this.$refs.foodsWrapper);
        this.menuScroll = new BScroll(this.$refs.menuWraper, {
          click: true
        });
        this.foodScroll = new BScroll(this.$refs.foodsWrapper, {
          probeType: 3,
          click: true
        });

        this.foodScroll.on('scroll', (position) => {
          this.scrollY = Math.abs(position.y);
//          console.log(this.scrollY);
//          console.log(this.currentIndex);
        });
      },
      _calculateHeight () {
        let foodList = this.$refs.foodList;
        let height = 0;
        this.listHeight.push(height);
        for (let i = 0; i < foodList.length; i++) {
          height += foodList[i].clientHeight;
          this.listHeight.push(height);
        }
//        console.log(this.listHeight);
      },
      _followScroll (index) {
        let dom = this.$refs.menuList[index];
        this.menuScroll.scrollToElement(dom, 300);
      },
      _selectMenu (index) {
        console.log('begin click');
        let dom = this.$refs.foodList[index];
        this.foodScroll.scrollToElement(dom, 300);
      }
    },
    created () {
      this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee'];
      this.$http.get('./api/goods').then(response => {
        console.log(response.data);
        response = response.body;
        if (response.errno === 0) {
          this.goods = response.data;
          this.$nextTick(() => {
            this._initScroll();
            this._calculateHeight();
          }
          );
        }
      });
    }
  };
</script>



